import React from 'react';
import styles from '../../styles/Landing/Profile.module.css'; 
import project_icon from '../../assets/images/logo512.png';
import profilePhoto from '../../assets/images/profilePhoto.png';

export default function Profile(props) {
    return (
        <div className={styles.profile}>
            <div className={styles.name_and_profile_img}>
                <div className={styles.profile_photo}><img src={profilePhoto} alt="" width="150px" height="150px"/></div>
                <div className={styles.name}>Mu Chen</div>
            </div>
            <p className={styles.profile_text}>An enthusiatstic front-end engineer who loves exploring the world of React.js</p>
        </div>
    );
}
